import React, {Component} from 'react';
import $ from 'jquery';


class User extends Component {
    componentDidMount(){

            $.ajax({
                type: "GET",
                url: "https://api.spotify.com/v1/me/",
                'Content-Type' : 'application/json',
                headers: {
                    'Authorization' : 'Bearer ' + this.props.token
                },
                success: (data) => {
                    console.log(data);
                    
                    this.setState({
                        email: data.email,
                        display_name: data.display_name,
                        href: data.href,
                        followers: data.followers.total,
                        id: data.id,
                        images: data.images.url,
                        product: data.product

                    });
                    console.log(this.state.images); 
                },
                fail: () => {
                    alert("API Playlist call failed.");
                } 
                
            })
    }

    state = {
        display_name: null,
        email: null,
        href: null, 
        id: null,
        followers: null,
        images: null,
        product: null
    }

    render(){
        return(
            <div>
            <h1>User</h1>

            <p><b>TOKEN</b>: {this.props.token}</p>
            <p><b>EMAIL</b>: {this.state.email}</p>
            <p><b>PROFILE HREF</b>: {this.state.href}</p>
            <p><b>ID</b>: {this.state.id}</p>
            <p><b>PRODUCT</b>: {this.state.product}</p>
            <p><b>FOLLOWERS</b>: {this.state.followers}</p>
            <p className="profile-picture"><b>Profile Picture</b>: </p>
            </div>

        )
    }   
}

export default User;