import React, {Component} from "react";
import $ from "jquery";
import "./Player.css";
// NOTE: This ajax call to the Spotify API returns:
// ALBUM(object), Playing(Bool), Progress (Int) 
class Player extends Component {

  state = {
    item: null,
    album: null,
    artists: null,
    song_name: null,
    song_uri: null,
    is_playing: null,
    progress_ms: null
  }

  componentDidMount(){
    this.getPlayerData();
    
  }

  getPlayerData(){
    $.ajax({
      url: "https://api.spotify.com/v1/me/player",
      type: "GET",
      beforeSend: (xhr) => {
        xhr.setRequestHeader("Authorization", "Bearer " + this.props.token);
      },
      success: (data) => {
        if(!data){
          return;
        }
        this.setState({
          item: data.item,
          album: data.item.album,
          artists: data.item.artists,
          song_name: data.item.name,
          song_uri: data.item.uri,
          is_playing: data.is_playing,
          progress_ms: data.progress_ms,
        });
        
      }
    });
  }
  
  render(){
    
    // Rendering will cause an error pre-flight
    // ternary operator to handle null error
    // Need to add Buttons and controls to connect to playbacksdk
    var player = (this.state.item != null) ? (
      <div>
        <h1>Currently Playing</h1>
        <p>Title: {this.state.song_name}</p>
        <p>Album: {this.state.album.name}</p>
        <img src={this.state.album.images[1].url}></img>

      </div>
    ) : (
      <h1>Nothing Playing</h1>
    )

    return (player);
  }
}

export default Player;