import React, {Component} from 'react';
import $ from "jquery";
import Button from "@material-ui/core/Button"

class playlists extends Component {
    constructor(props){
        super(props);

        this.state = {
            playlists : null
        }
    }
    componentDidMount(){
        this.fetchPlaylists();
    }

    fetchPlaylists = () => {
        // When the playlist tab is clicked, it will call an
        // ajax call that gets playlists. 
        $.ajax({
            url: "https://api.spotify.com/v1/me/playlists",
            type: "GET",
            beforeSend: (xhr) => {
            xhr.setRequestHeader("Authorization", "Bearer " + this.props.token);
            },
            success: (data) => {
                if(!data){
                    console.log("No Data!");
                }
                else {
                    this.setState({
                        playlists: data.items
                    })
                    //console.log("data saved to state: " + data.items)
                   
                }
            }
        });
    }

    //Method to display yourplaylists.
    //May need a ComponentWillUpdate method to call this.
    getCurrentState(){
        const playlists = this.state.playlists ? (
            this.state.playlists.map(playlist => {
                return(
                    <div>{playlist.name}</div>
                )
        })
    ) : (
            
                <div> No Playlists Found! </div>
            
        )
        return playlists;
    }
    render(){
        return(
        <div>
            <h1>Playlists</h1>
            <b>{this.getCurrentState()}</b>
            
        </div>
        )
    }
}

export default playlists;